import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, {ReactNode } from "react"
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {library,IconProp} from "@fortawesome/fontawesome-svg-core";
import {fas} from "@fortawesome/free-solid-svg-icons";
import {fab} from "@fortawesome/free-brands-svg-icons";
import './custom.css'

library.add(fas,fab)

interface State {
  current: number | undefined
}


class MyComponent extends StreamlitComponentBase<State> {
  public state = { current: undefined }

  componentDidMount(): void {
    super.componentDidMount()
    document.body.style.background = 'transparent'
  }

  render(): ReactNode {
    // Arguments
    const options: string[] = this.props.args['options']
    const btnIcons: string[] = this.props.args['btn_icons']
    const btnClasses: string[] = this.props.args['btn_classes']
    const classes: string = this.props.args['classes']
    const defaultIndex: number = this.props.args['default']

    // Computed values
    const current: number = this.state.current ?? defaultIndex

    return (
          <div className={`d-flex ${classes}`} style={{gap:'5px'}}>
        {options.map((option, idx) => (
          <button
            onClick={() => this.onClicked(idx)}
            className={`btn ${btnClasses[idx]}`}
            disabled={idx === current}
            key={idx}
          >
            <FontAwesomeIcon icon={`${btnIcons[idx]}` as IconProp} style={{marginRight:'5px'}}/>
            {option}
          </button>
        ))}
      </div>
    )
  }

  private onClicked = (idx: number): void => {
    this.setState(
      prevState => ({ current: idx }),
      () => Streamlit.setComponentValue(idx)
    )
  }
}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(MyComponent)
